@import url(https://fonts.googleapis.com/css?family=Poppins:100,100italic,200,200italic,300,300italic,regular,italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic);
@import url(https://fonts.googleapis.com/css?family=Bebas+Neue:regular);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

/* .body{
  zoom: 150%;
} */

.d-flex {
  display: flex;
  align-items: center;
}

.d-flex-column {
  display: flex;
  flex-direction: column;
}

.text-center {
  text-align: center;
}

.d-none {
  display: none;
}

.d-block {
  display: block;
}
.fb-login > span  {
  width: 100%!important;
  display: flex;
  justify-content: center;
}
.kep-login-facebook {
  border-radius: 30px!important;
  width: 90%!important;
}
.body {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  /* background-color: red; */
  width: 100vw;
  max-width: 600px;
  margin: 0 auto;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  background-color: #f5f5f5;
  min-height: 100vh;
}

button {
  cursor: pointer;
}

.login-header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: #fff;
  border-bottom: 5px solid #ee203b;
  padding: 1rem;
}

.login-header img {
  max-height: 3rem;
  margin-right: 1rem;
}

.neo-fan {
  font-family: "Bebas Neue", sans-serif;
  font-size: 2rem;
  letter-spacing: 2px;
}

.numeric{
   background-image: url(/static/media/Numeric_bg.7c0afffd.png);
   background-size: cover;
   background-position: center;
   min-height: 35vh;
   width: 100%;
}
.login .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem 0.5rem;
}

.login .container #instruction {
  color: #989898;
  font-weight: 500;
  text-align: center;
  font-size: 1.1rem;
}

.login .container .input-field {
  background-color: #ebebeb;
  width: 90%;
  margin: 1rem 0;
  display: flex;
  align-items: center;
  padding: 1rem 0.5rem;
  border-radius: 8px;
  font-size: 1.1rem;
}

.login .container .input-field input {
  padding: 0 0.5rem;
  border: 0;
  outline: none;
  background: transparent;
  font-size: 1.1rem;
}

.red-btn {
  background-color: #ee203b;
  border: 0;
  outline: none;
  width: 100%;
  text-align: center;
  padding: 0.8rem 0.5rem;
  font-size: 1.1rem;
  color: #fff;
  border-radius: 100px;
  box-shadow: 8px 8px 24px rgba(0, 0, 0, 0.1);
  margin: 0.5rem 0;
}

.login .btn {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0;
  outline: none;
  width: 90%;
  text-align: center;
  padding: 0.8rem 0.5rem;
  font-size: 1.2rem;
  font-weight: 500;
  border-radius: 100px;
  margin: 0.5rem 0;
}

.login .btn img {
  margin-right: 1rem;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.otp .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem 0.5rem;
}

.otp .container #instruction {
  color: #989898;
  font-weight: 500;
  text-align: center;
  font-size: 1.1rem;
}

.otp .container .input-fields {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-size: 1.1rem;
  width: 90%;
  margin: 1rem 0;
}

.otp .container .input-fields input {
  width: 3rem;
  font-size: 1.1rem;
  aspect-ratio: 1;
  text-align: center;
  background-color: #ebebeb;
  border: 0;
  border-bottom: 3px solid #c4c4c4;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  background-color: #f5f5f5;
}

.header .left {
  display: flex;
  align-items: center;
}

.header .left img {
  width: 100%;
  height: 2.5rem;
  margin-right: 0.5rem;
}

.header .right {
  display: flex;
  align-items: center;
}

.avatar {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  overflow: hidden;
  background-color: rgb(255, 0, 0);
  cursor: pointer;
}

.silent-text {
  color: #757575;
}
.mantine-vos779 {
  border-radius: 15px;
  padding: 10px 0;
  max-width: 300px;
}

.mantine-121w2fi {
  margin-right: 10px;
}

.mantine-1vhreye {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.dash .prog-lead {
  padding: 1rem;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
}

.dash .mantine-15ssm7.__mantine-ref-tabActive {
  font-weight: 600;
  margin-top: 0.3rem;
}

.dash .mantine-15ssm7.__mantine-ref-tabActive::after {
  content: "";
  display: block;
  width: 45%;
  height: 2px;
  border-radius: 20px;
  margin-top: 0.4rem;
  background-color: black;
  -webkit-animation: grow 0.3s;
          animation: grow 0.3s;
}

.dash .mantine-15ssm7,
.dash .mantine-qi2p0q {
  padding: 0;
  font-size: 1rem;
  font-weight: 500;
  margin-right: 1rem;
  margin-top: -0.2rem;
  background-color: transparent;
}

.dash .tab-con {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dash .tab-con .block1 {
  background-color: #eaf5f2;
  padding: 1rem;
  width: 48%;
  color: #35bfa6;
  border-radius: 10px;
}
.dash .tab-con .block2 {
  background-color: #fff9d7;
  padding: 1rem;
  width: 48%;
  color: #ffb636;
  border-radius: 10px;
}

.dash .tab-con img {
  width: 4rem;
  aspect-ratio: 1;
  margin-left: auto;
}

.dash .qr-div {
  background-color: #fff;
  padding: 0.5rem 1.5rem;
  position: fixed;
  bottom: 0;
  width: 100%;
  max-width: 600px;
}

.dash .qr-div button {
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dash .qr-div button img {
  margin-right: 0.5rem;
}

@-webkit-keyframes grow {
  0% {
    width: 0;
  }
  100% {
    width: 45%;
  }
}

@keyframes grow {
  0% {
    width: 0;
  }
  100% {
    width: 45%;
  }
}

.lead .top-ranks {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.6rem;
  border-radius: 5px;
  margin: 0.5rem 0;
}

.lead .top-ranks.first {
  background-color: #ffb53656;
  border-left: 5px solid #ffb636;
}

.lead .top-ranks.second {
  background-color: #a8a5a546;
  border-left: 5px solid #a8a5a5;
}

.lead .top-ranks.third {
  background-color: #fa9e7338;
  border-left: 5px solid #fa7234;
}

.lead .top-ranks.others {
  padding: 0;
}

.quiz .quiz-info {
  padding: 1rem;
  background-color: #fff;
}

.quiz #quiz-title {
  color: red;
  font-size: 1.2rem;
  font-weight: 500;
}

.quiz .quiz-body {
  background-color: #fff;
  margin-top: 1rem;
  padding: 1rem;
}
.quiz .btns {
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  max-width: 600px;
  padding: 1rem;
}

.quiz .btns button {
  width: 50%;
  outline: none;
  border: 0;
  padding: 0.8rem;
  border-radius: 12px;
  font-weight: 500;
}

.quiz .btns #next {
  background-color: #ee203b;
  color: #fff;
}


.quiz .dot {
  height: 0.5rem;
  width: 0.5rem;
  background-color: #c4c4c4;
  border-radius: 50%;
  margin: 0 0.5rem;
}

.quiz .dot.active {
  background-color: #ee203b;
  -webkit-transform: scale(2);
          transform: scale(2);
  -webkit-animation: sizeup 0.2s;
          animation: sizeup 0.2s;
}

.quiz .qes-no {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid #ee203b;
  height: 3rem;
  width: 3rem;
  border-radius: 8px;
  color: #ee203b;
  font-size: 1.1rem;
  font-weight: 500;
}

.quiz .quiz-body {
  padding-bottom: 5rem;
}

.quiz .quiz-body .ques {
  width: 95%;
  text-align: start;
  margin: 1rem auto;
  margin-bottom: 0;
  -webkit-columns: #4f4f4f;
          columns: #4f4f4f;
}

.quiz .ans {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  flex-wrap: wrap;
  cursor: pointer;
}

.quiz .ans.selected {
  background-color: #ee203b;
}

.quiz .option {
  background-color: #f8f8f9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 47%;
  border-radius: 10px;
  height: 8rem;
  margin-top: 1rem;
}

@-webkit-keyframes sizeup {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}

@keyframes sizeup {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(2);
            transform: scale(2);
  }
}

